<template>
  <div>
    <PersonalHeader></PersonalHeader>
    <div class="centerBg">
      <div class="wrapper clearfix">
        <div class="centerLeft">
          <PersonalMenu class="fl" currentMenu="6"></PersonalMenu>
        </div>
        <div class="centerRight">
          <div class="centerTitle">
            <ul>
              <router-link to="/account">
                <li >账户中心</li>
              </router-link>
              <router-link to="/investorManage">
                <li>投资者管理</li>
              </router-link>
              <router-link to="/leaveManage">
                <li class="active" >留言管理</li>
              </router-link>
            </ul>
            <!-- todo 增加跳转 -->
            <span class="brandBtn">我的品牌页 &gt;</span>
          </div>
          <div class="invBox">
            <ul class="invNav clearfix" @click="tabChange">
              <li :class="[read == 0 ? 'active':'']" data-read="0">未回复</li>
              <li :class="[read == 1 ? 'active':'']" data-read="1">已回复</li>
              <li :class="[read == 4 ? 'active':'']" data-read="4">已删除</li>
            </ul>
            <div class="invList">
              <dl v-for="(item,index) in data" :key="'man'+item.id">
                <dt class="clearfix">
                  <div class="data fl">{{item.name}}<span>|</span>{{item.sex == 1? '男':'女'}}<span>|</span>{{item.phone}}</div>
                  <div class="status fr">
                    <i>{{item.time}}</i>
                    <span @click="detail" :data-index="index">查看</span>
                  </div>
                </dt>
                <dd>{{item.content}}</dd>
              </dl>
            </div>

            <el-pagination background layout="prev, pager, next" :total="count" class="pages" @current-change="pageChange"></el-pagination>

          </div>
        </div>
      </div>
    </div>
    <el-dialog
      :title="title"
      :visible.sync="dialogVisible"
      width="40%"
      class="model1" center>
      <div class="box">
        <span>姓名：{{currentData.consult.name}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
        <span>性别：{{currentData.consult.sex == 1? '男':'女'}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
        <span>电话：{{currentData.consult.phone}}</span>
        <div class="consult">
          {{currentData.consult.content}}
        </div>
      </div>
      <div class="box">

        <div>客户跟踪日志：</div>
        <div class="logs" v-if="currentData.con_log.length>0">
          <div class="log" v-for="item in currentData.con_log" :key="'log'+item.id">
            <div>{{item.content}}</div>
            <div class="time">{{item.addtime}}</div>
          </div>
        </div>
        <div>回复：</div>
        <textarea type="textarea" v-model="content" class="addlog" placeholder="请在此进行回复~"></textarea>
        <span slot="footer" class="dialog-footer">
          <el-button @click="delConsult"  >{{read == 4? '恢复留言':'删除留言'}}</el-button>
            <el-button type="primary" @click="addConsult" >保 存</el-button>
        </span>
      </div>

    </el-dialog>
  </div>
</template>

<script>
  import PersonalHeader from '@/components/PersonalHeader'
  import PersonalMenu from '@/components/PersonalMenu'
  import api from '@/api/personal-api'

  export default {
    name: "index.vue",
    data(){
      return{
        dialogVisible:false,
        type:0,
        count:0,
        data:'',
        page: 0,
        read: 0,
        status: 1,
        currentData: {
          consult:{
            name:'',
            content:'',
            read:'',
            addtime:'',
          },con_log:[]
        },
        value:'0',
        detailName: ['未回复','已回复','已查看','','已删除'],
        title:'',
        content: ''
      }
    },
     created(){
        this.getData();
     },
    methods: {
      getData: function () {
        let {status, page,read} = this;
        if(read == 4){
          status = 0;
        }
        api.getInvertorManage({type: 1, status,read, page}).then(data => {
          this.data = data.data;
          this.count = data.count
        });
      },

      tabChange: function({target: {dataset:{read}}}){
        this.read = read;
        this.page = 0;
        this.getData();
      },

      /**
       * 点击分页
       * @param e
       */
      pageChange: function (e) {
        this.page = e - 1;
        window.scrollTo(0, 0);
        this.getData();
      },

      /**
       * 查看详情
       */
      detail: function ({currentTarget: {dataset: {index}}}) {
        api.getConsultDetail({id:this.data[index].id,type:1}).then(data =>{
          this.currentData = data;
          this.title = this.detailName[data.consult.read];
          this.dialogVisible = true;
        })
      },

      /**
       * 留言回复
       */
      addConsult: function(){
        const {id} = this.currentData.consult;
        const {content} = this;
        api.addConsult({id,content,type: 1}).then(data=>{
          this.dialogVisible = false;
          this.content = '';
          this.$message.success('保存成功')
        });

      },

      /**
       * 留言回复
       */
      delConsult: function(){
        const {id} = this.currentData.consult;
        api.delConsult({id}).then(data=>{
          this.dialogVisible = false;
          if(this.read == 4){
            this.$message.success('恢复成功')
          }else{
            this.$message.success('删除成功')
          }
          this.getData();
        });
      }
    },
    components: {
      PersonalHeader,
      PersonalMenu
    }
  }
</script>

<style lang="less" scoped>
  .centerBg{
    width: 100%;
    height: auto;
    padding: 20px 0 100px;
    background: #F4F5FA;
  }
  .centerLeft{
    float: left;
    width: 230px;
  }
  .centerRight{
    float: right;
    width: 950px;
    min-height: 610px;
    background: #fff;
    .centerTitle{
      padding: 0 33px;
      height: 60px;
      border-bottom: 1px solid #E3E4E5;
      ul{
        li{
          height: 58px;
          line-height: 58px;
          float: left;
          color: #333;
          font-size: 16px;
          border-bottom: 2px solid #fff;
          margin-right: 50px;
        }
        li.active{
          border-bottom: 2px solid #4F74F4;
        }
      }
      .brandBtn{
        float: right;
        color: #666;
        line-height: 60px;
      }
    }
    .invBox{
      .invNav{
        width: 100%;
        height: 60px;
        line-height: 60px;
        background: #F0F7FF;
        margin-bottom: 20px;
        li{
          float: left;
          padding: 0 40px 0 33px;
          color: #666;
          cursor: pointer;
        }
        li.active{
          color: #5389F7;
        }
      }
      .invList {
        padding: 0 30px;
        dl{
          width: 100%;
          padding: 0 0 18px;
          border-bottom: 1px dashed #E3E4E5;
          dt{
            height: 65px;
            line-height: 65px;
            .data{
              color: #333;
              span{
                margin: 0 15px;
                color: #D7D8D9;
              }
            }
            .status{
              i{
                color: #999;
                font-style: normal;
              }
              span{
                color: #5389F7;
                margin-left: 25px;
                cursor: pointer;
              }
            }
          }
          dd{
            padding: 20px 22px;
            color: #808080;
            background: #F4F5FA;
          }
        }

      }
    }
  }
  .pages{
    margin: 30px 0;
    text-align: center;
  }

  /* 弹窗样式 */
  .model1 {
  .box {
    font-size: 14px;
    color: rgba(51, 51, 51, 1);
    line-height: 24px;
    padding: 25px 45px;
    border-top: 1px solid #E3E4E5;
  span,label{
    cursor: pointer;
  }
  .title{
    cursor: default;
  }
  .consult {
    padding: 20px 23px;
    background-color: #F4F5FA;
    margin-top: 20px;
  }

  .logs {
    padding: 13px;
    border: 1px solid #E3E4E5;

  .time {
    color: #999;
    text-align: right;
  }
  }

  }
  }
  .addlog {
    width: 100%;
    padding: 23px;
    background-color: #F4F5FA;
    min-height: 90px;
    margin-top: 20px;
    resize: none;
    box-sizing: border-box;
  }

  .dialog-footer {
    display: flex;
    justify-content: center;
    margin-top: 30px;

  }
</style>
<style>

  .el-input{
    width: 345px;
  }
</style>



